.modal__wrap {
  position: fixed;
  width: 100%;
  max-width: 1280px;
  bottom: 0;
  z-index: 1001;
  background-color: transparent;
}

.main-subscribe-block {
  position: absolute;
  right: 0;
  bottom: 190px;
  width: 328px;
  border-radius: 5px 5px 0 0;
  background-color: #ffffff;
  padding: 16px 34px 21px;
  backface-visibility: hidden;
  will-change: transform;
  transform: translateY(200%);
  transition: transform 0.25s ease-in-out;
  transition: transform 0.25s ease-in-out;

  @include mobile-sm-max {
    width: 100%;
  }
}

.is-subscribe-open.main-subscribe-block {
  box-shadow: 0 -5px 20px 0 rgba(0, 0, 0, 0.1);
  transform: translateY(100%);
  transition: transform 0.5s ease-in-out;
  transition: transform 0.5s ease-in-out;
}

.subscribe__input-wrapper {
  position: relative;
  padding-right: 58px;
}

.main-subscribe-block__title {
  font-size: 16px;
  font-weight: 600;
  letter-spacing: 1.3px;
  color: #1c1c1c;
  text-transform: uppercase;
  margin: 0 0 7px 0;
}

.main-subscribe-block__subscribe-title {
  font-size: 12px;
  margin-bottom: 3px;
}

.main-subscribe-block__t {
  font-size: 12px;
  margin: 9px 0 11px;
}

.main-subscribe-block__btn {
  width: 100%;
  font-size: 14px;
  padding: 9px 0 8px;
}

.main-subscribe-block .subscribe {
  max-width: none;
}

.main-subscribe-block .subscribe__input {
  width: 100%;
  border-radius: 3px;
  padding: 5px 10px 6px;
  height: 34px;

  color: #1c1c1c;
  border: 1px solid #fff;
  border-color: rgba(220, 33, 51, 0.2);
}

.main-subscribe-block .subscribe__btn {
  height: 34px;
  background: #dc2133;
}

.btn_telegram {
  display: inline-block;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  border: none;
  text-decoration: none;
  background: #dc2133;
  box-shadow: 0 2px 10px 0 rgba(220, 33, 51, 0.3);
  border-radius: 5px;
  color: #fff;
  font-size: 12px;
  line-height: 1.1;
  font-weight: normal;
  padding: 7px 10px 8px;
}

.subscribe__btn {
  position: absolute;
  right: 0;
  top: 0;
  height: 38px;
  width: 59px;
  border: none;
  padding: 0;
  border-radius: 0 3px 3px 0;
  background-color: #1c1c1c;
  height: 34px;
  background: #dc2133;
  cursor: pointer;
}

.subscribe__btn::before {
  content: '';
  position: absolute;
  left: 50%;
  top: 50%;
  margin: -10px 0 0 -13px;
  width: 23px;
  height: 20px;
  background: url('../img/btn-svg.svg') 0 0 no-repeat;
  background-size: 100% 100%;
}

.close-btn {
  position: absolute;
  right: 15px;
  top: 20px;
  display: inline-block;
  vertical-align: middle;
  border: none;
  background: none;
  padding: 0;
  width: 15px;
  height: 15px;
  color: #1c1c1c;
  cursor: pointer;
}

.close-btn::before,
.close-btn::after {
  content: '';
  position: absolute;
  left: 50%;
  top: 50%;
  width: 15px;
  border-bottom: 2px solid;
  margin: -1px 0 0 -7px;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

.close-btn::after {
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.modal__success-wrap {
  position: fixed;
  display: none;
  justify-content: center;
  align-items: center;
  top: 0;
  left: 0;
  z-index: 9999;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  overflow: auto;
  text-align: center;
  vertical-align: middle;
  background-color: rgba(0, 0, 0, 0.5);
}

.modal__success {
  position: relative;
  display: inline-block;
  width: 320px;
  padding: 30px;
  min-height: 200px;
  color: black;
  font-size: 20px;
  background-color: white;
  border-radius: 3px;

  h3 {
    font-size: 16px;
    margin-top: 20px;
    margin-bottom: 25px;
    line-height: 18px;
    color: black;
    font-weight: bold;
  }

  p {
    font-size: 12px;
    line-height: 14px;
    color: grey;
    font-weight: normal;
    text-align: left;
  }

  &-close {
    position: absolute;
    top: 10px;
    right: 10px;
    width: 15px;
    height: 15px;
    border: none;
    background-color: transparent;
    cursor: pointer;

    &::after, &::before {
      content: '';
      position: absolute;
      left: 50%;
      top: 50%;
      width: 15px;
      border-bottom: 2px solid;
      border-color: grey;
      margin: -1px 0 0 -7px;
      transform: rotate(45deg);
    }

    &::after {
      transform: rotate(-45deg);
    }
  }
}
